import React, { Component } from 'react'

  import Navbar from 'react-bootstrap/Navbar';
  import NavbarBrand from 'react-bootstrap/esm/NavbarBrand';
  import Container from 'react-bootstrap/Container';
  import Nav from 'react-bootstrap/Nav';
  import { Twirl as Hamburger } from 'hamburger-react';
  import Logo from '../../images/55.png'
  import {BrowserRouter as Router, Route, Link, Routes} from 'react-router-dom';

  class Navbars extends Component{
    render(){
        return(
            
          <div>
    

    
            <Navbar bg='dark' expand="lg" variant='dark' fixed="top"   >
              
              <Container fluid>
              <NavbarBrand href="#" width="40px" height="40px" direction="left" className='mx-5'>
                 <img src={Logo} width={60} /> AT 
                  </NavbarBrand>
                
                   
                   <Navbar.Toggle aria-controls="basic-navbar-nav "  >
                     <Hamburger direction="right" color="#fff"  />
                   </Navbar.Toggle>
                       <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end mx-5">
                           <Nav className="mx-5" activeKey="/home">
                           <Link className='nav-link' to='/Main'>Home</Link>
                           <Link className='nav-link' to='/MyCourse' >My Course</Link>
                           <Link className='nav-link' to='/About' >About</Link>
                           <Link className='nav-link' to='/Contact' >Contact</Link>

                           <Link className='nav-link' to='/Admin/Login' target="_blank">Login</Link>
                              </Nav>
                    </Navbar.Collapse>
                 
                    
                </Container>
            </Navbar>

           
    </div>
        )
    }
}

export default Navbars